// app/(dashboard)/account/petmanagement/page.tsx
'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardMedia,
  CardContent,
  CardActions,
  Button,
  Chip,
  IconButton,
  Menu,
  MenuItem,
  Alert,
  Fab,
} from '@mui/material';
import {
  Add,
  MoreVert,
  Edit,
  Delete,
  Visibility,
  ArrowBack,
} from '@mui/icons-material';
import useUserProfile from '@/hooks/useUserProfile';
import PageHeader from '@/components/ui/headers/PageHeader';
import AddPetModal from '@/components/pet/petModal/AddPetModal';

type Pet = {
  id: string;
  name: string;
  type: string;
  breed?: string;
  gender: string;
  size: string;
  ageGroup: string;
  description?: string;
  status: string;
  images: { url: string; orderIdx: number }[];
  createdAt: string;
  updatedAt: string;
};

export default function PetManagementPage() {
  const router = useRouter();
  const { user, isLoading } = useUserProfile();
  const [pets, setPets] = useState<Pet[]>([]);
  const [isLoadingPets, setIsLoadingPets] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [menuAnchor, setMenuAnchor] = useState<null | HTMLElement>(null);
  const [selectedPetId, setSelectedPetId] = useState<string | null>(null);

  // Redirect if user is not a pet owner
  useEffect(() => {
    if (!isLoading && user && user.userType !== 'petOwner') {
      router.push('/account');
    }
  }, [user, isLoading, router]);

  // Fetch user's pets
  useEffect(() => {
    const fetchPets = async () => {
      if (!user?.id) return;

      try {
        setError(null);
        const response = await fetch('/api/pets');
        
        if (!response.ok) {
          throw new Error('Failed to fetch pets');
        }

        const data = await response.json();
        setPets(data.pets || []);
      } catch (error) {
        console.error('Error fetching pets:', error);
        setError('Failed to load your pets');
      } finally {
        setIsLoadingPets(false);
      }
    };

    if (user?.userType === 'petOwner') {
      fetchPets();
    }
  }, [user]);

  const handleAddPet = async (petData: any) => {
    try {
      const response = await fetch('/api/pets', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(petData),
      });

      if (!response.ok) {
        throw new Error('Failed to add pet');
      }

      // Refresh pets list
      const updatedResponse = await fetch('/api/pets');
      if (updatedResponse.ok) {
        const data = await updatedResponse.json();
        setPets(data.pets || []);
      }

      setIsModalOpen(false);
    } catch (error) {
      console.error('Error adding pet:', error);
      setError('Failed to add pet');
    }
  };

  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>, petId: string) => {
    setMenuAnchor(event.currentTarget);
    setSelectedPetId(petId);
  };

  const handleMenuClose = () => {
    setMenuAnchor(null);
    setSelectedPetId(null);
  };

  const handleDeletePet = async () => {
    if (!selectedPetId) return;

    try {
      const response = await fetch(`/api/pets/${selectedPetId}`, {
        method: 'DELETE',
      });

      if (!response.ok) {
        throw new Error('Failed to delete pet');
      }

      // Remove from local state
      setPets(pets.filter(pet => pet.id !== selectedPetId));
      handleMenuClose();
    } catch (error) {
      console.error('Error deleting pet:', error);
      setError('Failed to delete pet');
    }
  };

  const handleViewPet = (petId: string) => {
    router.push(`/pets/${petId}`);
  };

  const handleEditPet = (petId: string) => {
    // TODO: Implement edit functionality
    console.log('Edit pet:', petId);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'available':
        return 'success';
      case 'pending':
        return 'warning';
      case 'adopted':
        return 'default';
      default:
        return 'default';
    }
  };

  const formatPetInfo = (pet: Pet) => {
    const parts = [];
    if (pet.breed) parts.push(pet.breed);
    parts.push(pet.type);
    parts.push(pet.ageGroup);
    return parts.join(' â€¢ ');
  };

  // Loading state
  if (isLoading || isLoadingPets) {
    return (
      <Box p={3} display="flex" justifyContent="center">
        <Typography>Loading...</Typography>
      </Box>
    );
  }

  // Access control - only pet owners can access this page
  if (!user || user.userType !== 'petOwner') {
    return (
      <Box p={3} display="flex" justifyContent="center">
        <Typography>Access denied. Only pet owners can access this page.</Typography>
      </Box>
    );
  }

  return (
    <Box sx={{ maxWidth: 1200, mx: 'auto', p: 2 }}>
      {/* Header with back button */}
      <Box display="flex" alignItems="center" mb={2}>
        <IconButton onClick={() => router.back()} sx={{ mr: 2 }}>
          <ArrowBack />
        </IconButton>
        <Box flex={1}>
          <PageHeader 
            title="My Pet Management" 
            subtitle="Manage your pets looking for loving homes"
            centered={false}
          />
        </Box>
      </Box>

      {/* Error Alert */}
      {error && (
        <Alert severity="error" sx={{ mb: 3 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {/* Add Pet Button */}
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h6">
          {pets.length} {pets.length === 1 ? 'Pet' : 'Pets'}
        </Typography>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={() => setIsModalOpen(true)}
          sx={{
            backgroundColor: '#ed9426',
            '&:hover': { backgroundColor: '#d4841f' },
          }}
        >
          Add Pet
        </Button>
      </Box>

      {/* Pets Grid */}
      {pets.length === 0 ? (
        <Card sx={{ p: 6, textAlign: 'center' }}>
          <Typography variant="h6" color="text.secondary" gutterBottom>
            No pets added yet
          </Typography>
          <Typography variant="body2" color="text.secondary" mb={3}>
            Start by adding your first pet to help them find loving homes
          </Typography>
          <Button
            variant="contained"
            startIcon={<Add />}
            onClick={() => setIsModalOpen(true)}
            sx={{
              backgroundColor: '#ed9426',
              '&:hover': { backgroundColor: '#d4841f' },
            }}
          >
            Add Your First Pet
          </Button>
        </Card>
      ) : (
        <Grid container spacing={3}>
          {pets.map((pet) => (
            <Grid size={{ xs: 12, sm: 6, md: 4 }} key={pet.id}>
              <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                {pet.images.length > 0 && (
                  <Box
                    sx={{
                      width: '100%',
                      height: { xs: 152, md: 192 }, // h-38 = 152px, md:h-48 = 192px
                      position: 'relative',
                      overflow: 'hidden',
                      borderRadius: '12px', // rounded-xl
                      mb: 0,
                    }}
                  >
                    <CardMedia
                      component="img"
                      image={pet.images[0].url}
                      alt={pet.name}
                      sx={{
                        width: '100%',
                        height: '100%',
                        objectFit: 'cover',
                      }}
                    />
                  </Box>
                )}
                
                <CardContent sx={{ flexGrow: 1 }}>
                  <Box display="flex" justifyContent="space-between" alignItems="start" mb={1}>
                    <Typography variant="h6" component="h3" noWrap>
                      {pet.name}
                    </Typography>
                    <IconButton
                      size="small"
                      onClick={(e) => handleMenuOpen(e, pet.id)}
                    >
                      <MoreVert />
                    </IconButton>
                  </Box>

                  <Typography variant="body2" color="text.secondary" gutterBottom>
                    {formatPetInfo(pet)}
                  </Typography>

                  <Box display="flex" gap={1} mt={1} mb={2} flexWrap="wrap">
                    <Chip
                      label={pet.status}
                      size="small"
                      color={getStatusColor(pet.status)}
                    />
                    <Chip label={pet.size} size="small" variant="outlined" />
                  </Box>

                  {pet.description && (
                    <Typography 
                      variant="body2" 
                      color="text.secondary"
                      sx={{
                        display: '-webkit-box',
                        WebkitLineClamp: 3,
                        WebkitBoxOrient: 'vertical',
                        overflow: 'hidden',
                      }}
                    >
                      {pet.description}
                    </Typography>
                  )}
                </CardContent>
                
                <CardActions sx={{ mt: 'auto', pt: 0 }}>
                  <Button 
                    size="small" 
                    startIcon={<Visibility />}
                    onClick={() => handleViewPet(pet.id)}
                  >
                    View
                  </Button>
                  <Button 
                    size="small" 
                    startIcon={<Edit />}
                    onClick={() => handleEditPet(pet.id)}
                  >
                    Edit
                  </Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      )}

      {/* Context Menu */}
      <Menu
        anchorEl={menuAnchor}
        open={Boolean(menuAnchor)}
        onClose={handleMenuClose}
      >
        <MenuItem onClick={() => { 
          handleEditPet(selectedPetId!); 
          handleMenuClose(); 
        }}>
          <Edit fontSize="small" sx={{ mr: 1 }} />
          Edit
        </MenuItem>
        <MenuItem 
          onClick={handleDeletePet} 
          sx={{ color: 'error.main' }}
        >
          <Delete fontSize="small" sx={{ mr: 1 }} />
          Delete
        </MenuItem>
      </Menu>

      {/* Add Pet Modal */}
      <AddPetModal
        open={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        onSubmit={handleAddPet}
      />

      {/* Floating Action Button for mobile */}
      <Fab
        color="primary"
        onClick={() => setIsModalOpen(true)}
        sx={{
          position: 'fixed',
          bottom: 80, // Above navigation bar
          right: 16,
          backgroundColor: '#ed9426',
          '&:hover': { backgroundColor: '#d4841f' },
          display: { xs: 'flex', sm: 'none' }, // Only show on mobile
        }}
      >
        <Add />
      </Fab>
    </Box>
  );
}